import Vue from 'vue'
import Router from 'vue-router'
import Install from '../components/routers/start/install.vue'
import Alert from '../components/routers/components/alert/alert.md'
import Toast from '../components/routers/components/toast/toast.md'
import Grid from '../components/routers/components/grid/grid.md'
import Button from '../components/routers/components/button/button.md'
import Tooltip from '../components/routers/components/tooltip/tooltip.md'
import Icon from '../components/routers/components/icon/icon.md'
import Dropdown from '../components/routers/components/dropdown/dropdown.md'
import Popup from '../components/routers/components/popup/popup.md'
import Mask from '../components/routers/components/mask/mask.md'
import Loader from '../components/routers/components/loader/loader.md'
import Dom from '../components/routers/tools/dom/dom.md'

Vue.use(Router)

const routes = [
  {
    path: '/',
    redirect: '/start/install'
  },
  {
    path: '/start/install',
    component: Install
  },
  {
    path: '/components/alert',
    component: Alert
  },
  {
    path: '/components/toast',
    component: Toast
  },
  {
    path: '/components/grid',
    component: Grid
  },
  {
    path: '/components/button',
    component: Button
  },
  {
    path: '/components/icon',
    component: Icon
  },
  {
    path: '/components/tooltip',
    component: Tooltip
  },
  {
    path: '/components/dropdown',
    component: Dropdown
  },
  {
    path: '/components/popup',
    component: Popup
  },
  {
    path: '/components/mask',
    component: Mask
  },
  {
    path: '/components/loader',
    component: Loader
  },
  {
    path: '/tools/dom',
    component: Dom
  }
]

export default new Router({
  routes
})



